const { default: styled } = require("styled-components");

const BannerWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap-reverse; */
  justify-content: center;
  align-items: center;
`;

const BannerBackgroundWrapper = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: auto;
  height: auto;
  /* @media (min-width: 1140px) {
    flex: 1;
    height: 100%;
  } */
`;

const BannerBackground = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
`;

const BannerTextWrapper = styled.div`
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  @media (min-width: 1140px) {
    flex: 1;
  }
`;
const BannerText = styled.div`
  font-size: 3.5rem;
  line-height: 4.5rem;
  text-align: center;
`;
const BannerTextHilight = styled.span`
  font-size: 4.5rem;
  font-weight: 500;
  /* color: var(--primary); */
`;

const Home = () => (
  <BannerWrapper id="home">
    <BannerBackgroundWrapper>
      <BannerBackground src="/banner.jpg" />
    </BannerBackgroundWrapper>
    <BannerTextWrapper>
      <BannerText>
        Full-Stack <BannerTextHilight>Website Developer</BannerTextHilight>
      </BannerText>
    </BannerTextWrapper>
  </BannerWrapper>
);
export default Home;
